<template>
    <div class="page speakers">
       <h1>Our Speakers</h1>
        <div class="wrap">
            <div class="speaker" v-for="speaker in speakers" :key="speaker.name">
                <img :src="getImage(speaker.pic)">
                <div id="bio">
                    <h3>{{ speaker.name }}</h3>
                    <p>{{ speaker.title }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                speakers: [
                    {
                        name: 'Evan You',
                        title: 'CEO and Founder of VueJS',
                        pic: 'evan.jpg'
                    },
                    {
                        name: 'Sarah Drasner',
                        title: 'Consultant',
                        pic: 'sarah.jpg'
                    },
                    {
                        name: 'Sean Larkin',
                        title: 'Webpack Maintainer',
                        pic: 'sean.jpg'
                    },
                    {
                        name: 'Alexandre Chopin',
                        title: 'Nuxt.js Co-author',
                        pic: 'alex.jpg'
                    },
                    {
                        name: 'Roman Kuba',
                        title: 'Lead Frontend Scientist at Codeship',
                        pic: 'roman.jpg'
                    },
                    {
                        name: 'Chris Fritz',
                        title: 'Vue.js Core Team Member',
                        pic: 'chris.jpg'
                    },
                    {
                        name: 'Pine Wu',
                        title: 'Author of Vetur',
                        pic: 'pine.jpg'
                    },
                    {
                        name: 'Guillaume Chau',
                        title: 'Vue.js Core Team Member',
                        pic: 'guillaume.jpg'
                    },
                    {
                        name: 'Filipa Lacerda',
                        title: 'Frontend Engineer at Gitlab',
                        pic: 'filipa.jpg'
                    }
                ]
            }
        },
        methods: {
            getImage(img) {
                return require('_img/speakers/' + img);
            }
        }
    }
</script>

<style scoped>

    .speaker {
        display: flex;
        background-color: #FCFCFC;
        padding: 8px;
        position:relative;
        margin-bottom: 20px;
        transition: all .3s;
        box-shadow: 0 1px 2px rgba(0,0,0,0.1);
    }
    .speaker:hover {
        background-color: #F7F7F7;
        cursor: pointer;
        box-shadow: 0 1px 4px rgba(0,0,0,0.3);
    }
    .speaker img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
    }
    #bio {
        margin-left: 20px;
        padding: 10px 0;
    }
    #bio h3 {
        font-size: 1.2em;
    }
    #bio p {
        padding: 10px 0;
        color: #F09872;
    }

</style>



