<template>
    <div class="page home">
       <h1>Welcome/Kia Ora</h1>
        <div class="wrap">
            <p>For the first time ever, VueConf is coming to New Zealand.</p>
            <p>Set over 2 days at the <router-link to="/venue">Viaduct Events Centre</router-link> on Auckland's Viaduct Harbour, the event is set to showcase the best of web development/design using the popular Javascript framework Vue.js.</p>
            <p>A plethora of speakers (rockstars in the web dev community) will adorn our shores bringing with them the latest views, techniques, and information related to and around Vue.js.</p>
            <p>Featured speakers include <router-link to="/speakers">Evan You</router-link> (Creator of Vue.js), <router-link to="/speakers">Sean Larkin</router-link> (Webpack Evangelist), and <router-link to="/speakers">Sarah Drasner</router-link> (SVG/CSS animator and consultant).</p>
            <p>If you would like to attend this one time event, then register your interest below.</p>

            <div class="register">
                <input type="text" placeholder="Your email address">
                <button>Register</button>
            </div>

            <div class="sponsors">
                <p>Proudly sponsored by</p>
                <div>
                    <img src="~_img/logos/logo-hp.png">
                    <img src="~_img/logos/logo-xero.png">
                    <img src="~_img/logos/logo-awd.png">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        
    }
</script>

<style scoped>

.home a {
    color: #AEC176;
    font-weight: bold;
}

.register {
    margin-top: 40px;
    padding: 1em;
    display: flex;
    justify-content: center;
}
.register input {
    flex: 0 1 70%;
    height: 30px;
    padding: 5px;
    font-size: 1.1em;
    outline: 0;
    color: #666;
    border: 1px solid #AEC176;
}
.register button {
    border: none;
    outline: 0;
    background-color: #AEC176;
    text-transform: uppercase;
    padding: 0 10px;
    color: #FFFFFF;
    transition: all .3s;
}
.register button:hover {
    background-color: #222831;
    cursor: pointer;
}

.sponsors {
    margin: 40px;
    text-align: center;
}
.sponsors div {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.sponsors img {
    height: 80px;
}

</style>



