<template>
    <div class="page venue">
       <h1>The Venue</h1>
       <div class="wrap">
           <h3>Viaduct Events Centre</h3>
           <h5>161 Halsey Street, Viaduct Marina, Auckland</h5>
           <figure>
              <img src="~_img/ve-centre-sm.jpg"> 
           </figure>
           <p>ANZ Viaduct Events Centre, located on Auckland’s vibrant waterfront, is a premium venue boasting seven stunning spaces, including central Auckland's largest banquet space with capacity for up to 1,200 guests.</p>
           <p>With 6,000m² of space spanning three levels, the building has been designed to a five star environmental standard and makes the most of its impressive waterfront location with glass walls, balconies and easy access to the wharf.</p>
           <figure id="map">
               <img src="~_img/ve-centre-map.jpg">
           </figure>
       </div>
    </div>
</template>

<script>
    export default {
        
    }
</script>

<style scoped>

    .venue figure {
        height: 260px;
        object-fit: cover;
        overflow: hidden;
        margin-bottom: 20px;
    }
    .venue figure img {
        width: 100%;
        height: 100%;
    }
    .venue h3 {
        font-size: 1.3em;
    }
    .venue h5 {
        color: #F46060;
        padding: 5px 0;
        margin-bottom: 20px;
    }
    #map {
        margin: 20px 0;
    }

</style>



