<template>
    <div class="page about">
       <h1>Schedule</h1>
       <div class="wrap">
           <h3>Day 1, 9:00am - 4:00pm</h3>
           <ul>
               <li v-for="talk in day1" :key="talk.title">
                   <span>{{ talk.time }}</span>
                   <div class="talk">
                       <img v-if="talk.pic" :src="`./assets/img/${talk.pic}`">
                       <h3 v-if="talk.speaker">{{ talk.speaker }}</h3>
                       <p>{{ talk.title }}</p>
                   </div>
                </li>
           </ul>
       </div>
       <div class="wrap">
        <h3>Day 2, 10:00am - 3:00pm</h3>
        <ul>
            <li v-for="talk in day2" :key="talk.title">
                <span>{{ talk.time }}</span>
                <div class="talk">
                    <h3 v-if="talk.speaker">{{ talk.speaker }}</h3>
                    <p>{{ talk.title }}</p>
                </div>
             </li>
        </ul>
    </div>
    </div>
</template>

<script>
    export default {

        data() {
            return {
                day1: [
                    {
                        time: '9.00am',
                        speaker: '',
                        title: 'Welcome speech',
                        duration: 15
                    },
                    {
                        time: '9.15am',
                        speaker: 'Evan You',
                        title: 'Keynote speech',
                        duration: 30
                    },
                    {
                        time: '9.45am',
                        speaker: 'Chris Fritz',
                        title: 'Avoiding an awkward fit: how to best introduce Vue into your app and team',
                        duration: 30
                    },
                    {
                        time: '10.15am',
                        speaker: '',
                        title: 'Coffee break',
                        duration: 15
                    },
                    {
                        time: '10.30am',
                        speaker: 'Alexandre Chopin',
                        title: 'Server side rendering in Vue.js apps',
                        duration: 30
                    },
                    {
                        time: '11.00am',
                        speaker: 'Pine Wu',
                        title: 'Vetur = VSCode + Vue',
                        duration: 30
                    },
                    {
                        time: '11.30am',
                        speaker: 'Sean Larkin',
                        title: 'Code Splitting patterns in Vue.js',
                        duration: 30
                    },
                    {
                        time: '12.00pm',
                        speaker: '',
                        title: 'Lunch Break',
                        duration: 90
                    },
                    {
                        time: '1.30pm',
                        speaker: '',
                        title: 'Lightning Talks',
                        duration: 60
                    },
                    {
                        time: '2.30pm',
                        speaker: 'Filipa Lacerda',
                        title: 'How we made the Frontend awesome at Gitlab with Vue.js',
                        duration: 30
                    },
                    {
                        time: '3.00pm',
                        speaker: '',
                        title: 'Q & A Session',
                        duration: 45
                    },
                    {
                        time: '3.45pm',
                        speaker: '',
                        title: 'Closing speech',
                        duration: 15
                    }
                ],
                day2: [
                    {
                        time: '10.00am',
                        speaker: '',
                        title: 'Welcome speech',
                        duration: 15
                    },
                    {
                        time: '10.15am',
                        speaker: 'Guillaume Chau',
                        title: 'GraphQL made easy with Apollo and Vue',
                        duration: 45
                    },
                    {
                        time: '10.45am',
                        speaker: 'Roman Kuba',
                        title: 'Testing Vue components with Jest',
                        duration: 30
                    },
                    {
                        time: '11.15pm',
                        speaker: '',
                        title: 'Coffee break',
                        duration: 15
                    },
                    {
                        time: '11.30am',
                        speaker: '',
                        title: 'Open Workshop',
                        duration: 60
                    },
                    {
                        time: '12.30pm',
                        speaker: '',
                        title: 'Lunch break',
                        duration: 60
                    },
                    {
                        time: '1.30pm',
                        speaker: 'Sarah Drasner',
                        title: 'Animating Vue',
                        duration: 30
                    },
                    {
                        time: '2.00pm',
                        speaker: 'Evan You',
                        title: 'State of Vue.js',
                        duration: 30
                    },
                    {
                        time: '2.30pm',
                        speaker: '',
                        title: 'Final Q & A',
                        duration: 20
                    },
                    {
                        time: '2.50pm',
                        speaker: '',
                        title: 'Goodbye speech',
                        duration: 15
                    }
                ]
            }
        }
        
    }
</script>

<style scoped>

    .wrap ul {
        margin: 20px 0 20px 40px;
    }

    .wrap ul li {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        padding: 20px 0;
    }
    .wrap ul li:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 2px;
        height: 100%;
        background-color: #F3D179;
    }
    .wrap ul li div {
        margin-left: 20px;
        width: 100%;
        background-color: #F6F6F6
        /* border: 1px solid #F3D179; */
    }
    .wrap > h3 {
        background-color: #F3D179;
        padding: 10px;
        font-size: 1.2em;
        font-weight: bold;
    }
    .talk {
        font-size: .9em;
        padding: 5px;
    }
    .talk h3 {
        text-transform: uppercase;
        margin-bottom: 5px;
    }
    .talk p {
        padding: 0;
        line-height: 1.2em;
        font-weight: bold;
    }
    .wrap ul li span {
        display: block;
        width: 70px;
        margin-left: -35px;
        padding: 5px 0;
        text-align: center;
        background-color: #FFFFFF;
    }

</style>



